@charset "UTF-8";
:root {
  --tva-primary: #e9e9e1;
  --tva-primary-hover: #d5d514;
  --tva-primary-active: #d0d00f;
  --tva-secondary: #e9e9e1;
  --tva-secondary-background: #1b2c13;
  --tva-dark-green: #2A2920;
  --list-hover: #a7a5a1;
  --background-color: #e9e9e1;
  --foreground-color: #2A2920;
  --border: 1px solid #2A2920;
  --border-light: 1px solid #e9e9e1;
  --lineHeight: calc(var(--fontSize) * 1.5);
  --displayScale: 1;
  --pixel: 1px;
  --baseline: calc(var(--lineHeight) / 2);
  --blockSpacingTop: 0;
  --blockSpacingBottom: var(--lineHeight);
  --hrThickness: 1px;
  --primary-color: #e0e01f;
  --input-background:rgba(42, 41, 32, 0.1);
  --disabled: #a7a5a1;
  --fontSize: 14px;
  --h1-size: 2.5rem;
  --h2-size: 2rem;
  --h3-size: 1.7rem;
  --h4-size: 1.4rem;
  --ratio: 1.25;
}

@supports (font-variation-settings: normal) {
  :root {
    --sansFont: "neue-haas-unica", sans-serif;
  }
}
@media (min-resolution: 1.5dppx) {
  :root {
    --displayScale: 2;
    --pixel: 0.5px;
  }
}
@media (min-resolution: 2.5dppx) {
  :root {
    --displayScale: 3;
    --pixel: 0.34px;
  }
}
@media (min-resolution: 3.5dppx) {
  :root {
    --displayScale: 4;
    --pixel: 0.25px;
  }
}
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul[class],
ol[class] {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul[class],
ol[class] {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  width: 100%;
  display: block;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  background: none;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
button {
  background: 0 0;
  border: 0;
  outline: 0;
  color: var(--foreground-color);
}

a {
  cursor: pointer;
  background-color: transparent;
  padding: 0;
}

a,
a:visited {
  color: #000;
  text-decoration: none;
}

a:active,
a:hover {
  color: #000;
}

.touch a:active,
.touch a:hover {
  text-decoration: none;
}

li,
ol,
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.border-t {
  border-top: var(--border) !important;
}

.border-b {
  border-bottom: var(--border) !important;
}

.border-l {
  border-left: var(--border) !important;
}

.border-r {
  border-right: var(--border) !important;
}

.border {
  border: var(--border) !important;
}

.p-1 {
  padding: 1em;
}

.p-0 {
  padding: 0;
  padding-top: 0;
  padding-bottom: 0;
}

.border-t-w {
  border-top: var(--border-light) !important;
}

.border-b-w {
  border-bottom: var(--border-light) !important;
}

.border-l-w {
  border-left: var(--border-light) !important;
}

.border-r-w {
  border-right: var(--border-light) !important;
}

.border-w {
  border: var(--border-light) !important;
}

img {
  width: 100%;
}

.blur {
  filter: blur(8px);
}

.mt-s {
  margin-top: 10px;
}

.mb-s {
  margin-bottom: 10px;
}

a,
a:visited {
  color: var(--tva-dark-green);
  text-decoration: none;
}

a:active,
a:hover {
  color: var(--tva-dark-green);
}

a.left {
  position: relative;
}

a.left::before {
  content: "";
  position: absolute;
  width: 0;
  height: 1px;
  bottom: 0;
  left: 0;
  background-color: var(--foreground-color);
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}

a.left:hover::before {
  visibility: visible;
  width: 100%;
}

.main-container-wrapper {
  min-height: 100vh;
  height: 100%;
}

.disabled {
  cursor: not-allowed;
  opacity: 0.1 !important;
}

b,
strong,
.bold {
  font-weight: 600;
}

i,
em,
.italic {
  font-style: italic;
}

/* preformatted text */
pre,
code,
tt,
.code,
.monospace {
  font-family: var(--monoFont), Inconsolata, Menlo, monospace;
  font-weight: 430;
  word-wrap: break-word;
  white-space: pre-wrap;
}

pre b,
code b,
tt b,
.code b,
.monospace b {
  font-weight: 600;
  font-weight: 580;
}

pre {
  white-space: pre;
  word-wrap: normal;
  overflow-x: auto;
  display: block;
}

b pre,
b code,
b tt,
b .code,
b .monospace {
  font-weight: 600;
}

pre code,
pre tt {
  white-space: pre;
  word-wrap: normal;
}

.paragraph {
  font-size: 16px;
}

h1,
.h1 {
  font-weight: 400;
  letter-spacing: -0.02em;
  font-size: var(--h1-size);
  line-height: calc(var(--lineHeight) * 2);
  margin-left: calc(var(--h1-size) / -22);
  margin-top: calc(var(--lineHeight) * 2);
  margin-bottom: var(--lineHeight);
  word-break: break-word;
}

h1.single-line {
  margin-top: var(--lineHeight);
  padding-top: calc(var(--lineHeight) * 0.5);
  padding-bottom: calc(var(--lineHeight) * 0.5);
}

h3 + h1,
h4 + h1,
h3 + h1.single-line,
h4 + h1.single-line {
  margin-top: calc(var(--baseline) * 3);
}

h1.single-line:first-child {
  margin-top: 0;
  padding-bottom: calc(var(--lineHeight) * 0.5);
}

h2,
.h2 {
  font-weight: 400;
  letter-spacing: -0.03em;
  font-size: var(--h2-size);
  line-height: calc(var(--lineHeight) * 1.5);
  margin-left: calc(var(--h2-size) / -26);
  margin-bottom: var(--lineHeight);
  margin-top: var(--lineHeight);
}

h2.single-line {
  margin-top: var(--lineHeight);
  padding-top: calc(var(--lineHeight) * 0.5);
  padding-bottom: calc(var(--lineHeight) * 0.5);
  margin-bottom: 0;
}

h3 + h2,
h4 + h2,
h3 + h2.single-line,
h4 + h2.single-line {
  margin-top: var(--baseline);
}

h2.single-line:first-child {
  margin-top: unset;
}

h3,
.h3 {
  font-weight: 400;
  letter-spacing: -0.02em;
  font-size: var(--h3-size);
  margin-bottom: calc(var(--lineHeight) * 0.5);
  line-height: 30px;
}

h4,
.h4 {
  font-weight: 400;
  letter-spacing: -0.012em;
  font-size: var(--h4-size);
}

.thin {
  font-weight: 300 !important;
}

h3.single-line,
h4.single-line {
  padding-bottom: calc(var(--baseline) * 1.25);
  padding-top: calc(var(--baseline) * 1.25);
  margin-bottom: 0;
}

h3.single-line + h1.single-line,
h4.single-line + h1.single-line,
h3.single-line + h1,
h4.single-line + h1,
h3.single-line + h2.single-line,
h4.single-line + h2.single-line,
h3.single-line + h2,
h4.single-line + h2 {
  margin-top: var(--lineHeight);
}

h5,
h6,
.h5,
.h6 {
  font-weight: 400;
  letter-spacing: -0.015em;
  margin-bottom: 0;
}

/* large headers */
h1.xlarge,
.h1.xlarge {
  --h1-size: 3.5rem;
  line-height: calc(var(--lineHeight) * 2);
  font-weight: 400;
}

h1.xxlarge,
.h1.xxlarge {
  --h1-size: 7.5rem;
  line-height: calc(var(--lineHeight) * 4);
  font-weight: 500;
}

h1.xxxlarge,
.h1.xxxlarge {
  --h1-size: 10.5rem;
  line-height: calc(var(--lineHeight) * 5);
  font-weight: 500;
}

/* small text */
.small {
  font-size: 0.85rem;
  line-height: var(--lineHeight);
}

.large {
  font-size: 1.6rem;
  font-weight: 400;
  line-height: var(--lineHeight);
}

.medium {
  font-size: 1rem;
  font-weight: 500;
  line-height: var(--lineHeight);
}

.medium.single-line {
  margin-top: var(--lineHeight);
}

.xsmall {
  font-size: 0.8em;
  line-height: calc(var(--lineHeight) * 0.75);
  padding-top: calc(var(--lineHeight) * 0.25);
}

.xxsmall {
  font-size: 0.65em;
  line-height: calc(var(--lineHeight) * 0.7);
  padding-top: calc(var(--lineHeight) * 0.3);
}

.xxxsmall {
  font-size: 0.5em;
  line-height: calc(var(--lineHeight) * 0.5);
  padding-bottom: calc(var(--lineHeight) * 0.25);
}

h1 > a,
h2 > a,
h3 > a,
h4 > a,
h5 > a,
h6 > a,
.h1 > a,
.h2 > a,
.h3 > a,
.h4 > a,
.h5 > a,
.h6 > a {
  text-decoration: none;
}

h1 > a:hover,
h2 > a:hover,
h3 > a:hover,
h4 > a:hover,
h5 > a:hover,
h6 > a:hover,
.h1 > a:hover,
.h2 > a:hover,
.h3 > a:hover,
.h4 > a:hover,
.h5 > a:hover,
.h6 > a:hover {
  text-decoration: underline rgba(0, 0, 0, 0.3);
  color: inherit;
}

@media (max-width: 1280px) {
  h1.xxxlarge,
.h1.xxxlarge {
    --h1-size: 8rem;
  }

  h1.xlarge,
.h1.xlarge {
    --h1-size: 3rem;
  }

  .large {
    font-size: 1.3rem;
  }
}
@media (max-width: 560px) {
  h1,
.h1 {
    font-weight: 400;
    letter-spacing: -0.02em;
    font-size: 2.3em;
    line-height: calc(var(--lineHeight) * 2);
    margin-left: calc(var(--h1-size) / -22);
    margin-top: calc(var(--lineHeight) * 2);
    margin-bottom: var(--lineHeight);
    word-break: break-word;
  }

  h1.xxxlarge,
.h1.xxxlarge {
    --h1-size: 5rem;
    line-height: calc(var(--lineHeight) * 4);
  }

  h1.xlarge,
.h1.xlarge {
    --h1-size: 2.5rem;
  }

  .medium {
    font-size: 1rem;
  }

  .large {
    font-size: 1.4rem;
  }

  h2,
.h2 {
    --h2-size: 1.7rem;
  }

  h2.single-line {
    margin-top: var(--lineHeight);
    padding-top: calc(var(--lineHeight) * 0.5);
    padding-bottom: calc(var(--lineHeight) * 0.5);
    margin-bottom: 0;
  }

  h3,
.h3 {
    --h3-size: 1.5rem;
  }
}
header {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  position: fixed;
  top: 0;
  background: var(--background-color);
  z-index: 3;
  width: 100%;
}

.header-logo {
  grid-column: 1/3;
}
.header-logo svg.tva-desktop {
  width: 150px;
  height: auto;
  margin-top: 4px;
}
.header-logo svg.tva-desktop path {
  fill: var(--foreground-color);
}

.header-search {
  grid-column: 3/6;
  display: grid;
  grid-template-columns: 1fr 15fr;
}
.header-search svg.search {
  width: 20px;
  height: 20px;
  margin: 0 10px;
}

.nav-cart {
  display: grid;
  grid-column: 6/7;
  border-bottom: var(--border);
}

.nav-login {
  display: grid;
  grid-column: 7/8;
  border-bottom: var(--border);
}
.nav-login .user-menu {
  border-right: var(--border);
  align-items: center;
}

.nav-bar-mobile {
  grid-column: 8/9;
  display: grid;
  padding: calc(var(--fontSize) * 0.5);
  border-bottom: var(--border);
  place-items: center;
}
.nav-bar-mobile .burger {
  display: block;
  margin-bottom: 24px;
}
.nav-bar-mobile .burger div {
  position: relative;
  top: 12px;
  left: 0;
  width: 24px;
  height: 1px;
  display: block;
  background: black;
}
.nav-bar-mobile .burger div:before,
.nav-bar-mobile .burger div:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 1px;
  background: inherit;
  transition: transform 0.3s ease;
}
.nav-bar-mobile .burger div:before {
  transform: translateY(-6px);
}
.nav-bar-mobile .burger div:after {
  transform: translateY(6px);
}

.header-logo,
.header-search {
  border-right: var(--border);
  border-bottom: var(--border);
}

.header-logo,
.header-search,
.user-menu,
.cart {
  align-items: center;
  justify-content: center;
  display: flex;
}

#menu-mobile {
  display: none;
}

.cart-mobile {
  display: none !important;
}

.nav-bar-mobile.is-open .burger div {
  visibility: hidden;
}
.nav-bar-mobile.is-open .burger div:before {
  visibility: visible;
  transform: translateY(0) rotate(45deg);
}
.nav-bar-mobile.is-open .burger div:after {
  visibility: visible;
  transform: translateY(0) rotate(-45deg);
}

@media (max-width: 1000px) {
  .nav-bar-mobile {
    grid-column: 5/5;
    grid-row: 1;
    border-left: none;
  }

  #menu-mobile {
    display: grid;
  }

  header {
    grid-template-columns: repeat(4, 1fr);
  }

  .header-logo {
    grid-column: 1/2;
  }
  .header-logo svg.tva-desktop {
    width: 150px;
    height: auto;
    margin-top: 4px;
  }

  .header-search {
    grid-column: 2/4;
    grid-row: 1;
  }

  .nav-login,
#menu-desktop {
    display: none;
  }

  .nav-cart {
    display: grid;
    grid-column: 4/5;
    grid-row: 1;
    border-bottom: var(--border);
    border-right: var(--border);
  }
}
@media (max-width: 601px) {
  .header-logo {
    grid-column: 1/3;
    grid-row: 1;
    width: 67vw;
  }

  .header-search {
    grid-column: 1/5;
    grid-row: 2;
    z-index: 1;
    border-right: 0;
  }

  .nav-cart {
    display: grid;
    grid-column: 3/4;
    grid-row: 1;
  }

  .nav-bar-mobile {
    display: grid;
    grid-column: 4/5;
    grid-row: 1;
  }
}
@media (max-width: 600px) {
  .header-logo {
    grid-column: 1/2;
    grid-row: 1;
    width: 56vw;
  }

  .header-search {
    display: none;
  }

  .nav-cart {
    display: grid;
    grid-column: 2/4;
    grid-row: 1;
  }
  .nav-cart .name {
    font-size: 14px;
  }

  .nav-bar-mobile {
    display: grid;
    grid-column: 4/5;
    grid-row: 1;
  }
}
.cart-icon {
  position: relative;
  top: 3px;
}
.cart-icon svg {
  width: 24px;
  height: 27px;
  fill: var(--foreground-color);
}
.cart-icon .count {
  font-size: 1em;
}

.product-grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: var(--rowGap);
}

@media (max-width: 1100px) {
  .product-grid-2 {
    display: flex;
    flex-direction: column-reverse;
  }
  .product-grid-2 .tva-image-subpage {
    border-left: none;
  }
}
.article-information-subpage {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  grid-row: 1;
  grid-area: 1/1;
}
.article-information-subpage .product-subpage-information {
  padding: 2em;
}
.article-information-subpage .product-subpage-information .product-heading h1 {
  margin: 0px;
}
.article-information-subpage .product-subpage-information .stock-status {
  float: right;
}
.article-information-subpage .product-subpage-information .full-specifications {
  display: flex;
  flex-direction: row;
}
.article-information-subpage .product-subpage-information .full-specifications p {
  margin-right: 0.5em;
}
.product-card-subpage {
  grid-area: 2/2;
  grid-row: 1;
}
.product-card-subpage .article-image-subpage {
  width: 100%;
}

.row-cta {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.row-cta .article-cart-subpage {
  border-bottom: var(--border);
  border-top: var(--border);
  border-right: var(--border);
}
.row-cta .article-cart-subpage-now {
  border-bottom: var(--border);
  border-top: var(--border);
}

.attached-products-wrapper {
  margin-bottom: 94px;
}
.attached-products-wrapper .scroll {
  border-left: var(--border);
}
.attached-products-wrapper .scroll:nth-child(4n) {
  border-left: var(--border);
  border-right: var(--border);
}

.related-title {
  text-align: center;
  margin-top: var(--lineHeight);
  margin-bottom: 0;
  padding: 2em;
}

.featured-heading {
  width: 100%;
  text-align: center;
  border-top: var(--border);
  padding: 15px;
}
.featured-heading h1 {
  margin: 0;
}

.featured-pro {
  margin-top: -10px;
  margin-bottom: 10%;
}

@media (max-width: 1120px) {
  .featured-pro {
    margin-bottom: 222px;
  }
}
@media (max-width: 700px) {
  .featured-pro {
    margin-bottom: 132px;
  }
}
@media (max-width: 400px) {
  .featured-pro {
    margin-bottom: 97px;
  }
}
footer {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  transition: bottom 0.2s ease-in-out;
  width: 100%;
  background: var(--background-color);
  z-index: 2;
  position: relative;
  bottom: 0;
  left: 0;
}
footer a:not(.btn-hover),
footer p {
  text-align: center;
  padding: 11px 18px;
  margin: 0;
}
footer .footer-menu {
  grid-column: 1/4;
  display: grid;
  grid-row: 2;
  grid-template-columns: 1fr 1fr 1fr;
  border-bottom: var(--border);
  place-items: center;
  border-right: var(--border);
  border-top: var(--border);
}
footer .footer-newsletter {
  grid-column: 4/5;
  grid-row: 2;
}
footer .footer-newsletter .newsletter-cta {
  display: grid;
  border-top: var(--border);
  border-bottom: var(--border);
  min-height: 48px;
}
footer .footer-trade-mark {
  grid-row: 3;
  grid-column: 1/5;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
}

.form-grid {
  border-top: var(--border);
  display: grid;
}

@media (max-width: 768px) {
  footer .footer-newsletter-panel.is-open {
    grid-column: 1/5;
    grid-row: 1;
    bottom: 135px;
    left: 0;
    border-left: none;
    border-right: none;
    border-radius: 2em 2em 0 0;
  }
  footer .footer-menu {
    grid-column: 1/5;
    border-right: 0;
    grid-row: 3;
    border-bottom: var(--border);
    border-top: 0;
  }
  footer .footer-newsletter {
    grid-column: 1/5;
    grid-row: 2;
  }
  footer .footer-trade-mark {
    grid-column: 1/5;
    border-bottom: var(--border);
    grid-row: 4;
    display: flex;
    flex-direction: column;
    place-items: center;
  }
  footer .philo {
    border-top: var(--border);
    width: 100%;
  }
}
@media (max-width: 414px) {
  footer .footer-trade-mark {
    flex-direction: column;
  }
  footer .footer-newsletter-panel.is-open {
    bottom: 183px;
  }
}
@media (max-width: 376px) {
  footer .footer-trade-mark {
    flex-direction: column;
  }
  footer .footer-newsletter-panel.is-open {
    bottom: 203px;
  }
}
/* document */
html {
  scrollbar-width: none;
}

body {
  background: var(--background-color);
  color: var(--foreground-color);
  font-family: var(--sansFont), sans-serif;
  font-display: swap;
  font-size: var(--fontSize);
  line-height: var(--lineHeight);
  letter-spacing: -0.01em;
  text-size-adjust: 100%;
  font-variant-ligatures: contextual common-ligatures;
