.tva-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.tva-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 20px;
  border-radius: 99em 99em 0 0;
  background: var(--tva-dark-green);
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.tva-ellipsis div:nth-child(1) {
  left: 8px;
  animation: tva-ellipsis1 0.6s infinite;
}

.tva-ellipsis div:nth-child(2) {
  left: 8px;
  animation: tva-ellipsis2 0.6s infinite;
}

.tva-ellipsis div:nth-child(3) {
  left: 32px;
  animation: tva-ellipsis2 0.6s infinite;
}

.tva-ellipsis div:nth-child(4) {
  left: 56px;
  animation: tva-ellipsis3 0.6s infinite;
}

@keyframes tva-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes tva-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes tva-ellipsis2 {
