/** Light color-scheme **/

:root {
    --cc-bg: #e9e9e1;
    --cc-text: #2A2920;

    --cc-btn-primary-bg: #1b2c13;
    --cc-btn-primary-text: var(--cc-bg);
    --cc-btn-primary-hover-bg: #2A2920;
    --cc-btn-secondary-bg: #e9e9e1;
    --cc-btn-secondary-text: var(--cc-text);
    --cc-btn-secondary-hover-bg: #e9e9e1;

    --cc-toggle-bg-off: #2A2920;
    --cc-toggle-bg-on: var(--cc-btn-primary-bg);
    --cc-toggle-bg-readonly: #51554f;
    --cc-toggle-knob-bg: #a7a5a1;
    --cc-toggle-knob-icon-color: #c2c2b5;

    --cc-cookie-category-block-bg: #e9e9e1;
    --cc-cookie-category-block-bg-hover: #c2c2b5;

    --cc-section-border: #1b2c13;
    --cc-cookie-table-border: #1b2c13;

    --cc-overlay-bg: rgba(4, 6, 8, .85);
    --cc-webkit-scrollbar-bg: #2A2920;
    --cc-webkit-scrollbar-bg-hover: #1b2c13;
}

.cc_div *,
.cc_div :before,
.cc_div :after {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    float: none;
    font-style: inherit;
    font-variant: normal;
    font-weight: inherit;
    font-family: inherit;
    line-height: 1.2;
    font-size: 1em;
    transition: none;
    animation: none;
    margin: 0;
    padding: 0;
    text-transform: none;
    letter-spacing: unset;
    color: inherit;
    background: none;
    border: none;
    box-shadow: none;
    text-decoration: none;
    text-align: left;
    visibility: unset;
}

.cc_div {
    font-size: 16px;
    font-weight: 400;
    color: #2d4156;
    color: var(--cc-text);
}

.cc_div .c-bn,
.cc_div .b-tl,
#s-ttl,
#c-ttl,
#s-bl td:before {
    font-weight: bold;
}

.cc_div input,
.cc_div button,
.cc_div a {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.cc_div a {
    text-decoration: underline;
}

.cc_div a:hover {
    text-decoration: none;
}


/* Make elements "animatable" */

.c--anim #cm,
.c--anim #s-cnt,
.c--anim #s-inr,
#cs-ov,
#cm-ov {
    transition: visibility .25s ease, opacity .25s ease, transform .25s ease!important;
}

.c--anim .c-bn {
    transition: background-color .25s ease!important;
}


/* start transitions */

.c--anim #cm.bar.slide,
.c--anim .bar.slide #s-inr {
    transition: visibility .4s ease, opacity .4s ease, transform .4s ease!important;
}

.c--anim #cm.bar.slide+#cm-ov,
.c--anim .bar.slide+#cs-ov {
    transition: visibility .4s ease, opacity .4s ease, transform .4s ease!important;
}

#cm.bar.slide,
.cc_div .bar.slide #s-inr {
    transform: translateX(100%);
    opacity: 1;
}

#cm.bar.top.slide,
.cc_div .bar.left.slide #s-inr {
    transform: translateX(-100%);
    opacity: 1;
}

#cm.slide,
.cc_div .slide #s-inr {
    transform: translateY(1.6em);
}

#cm.top.slide {
    transform: translateY(-1.6em);
}

#cm.bar.slide {
    transform: translateY(100%);
}

#cm.bar.top.slide {
    transform: translateY(-100%);
}


/* end transitions */


/* Show modals */

.show--consent .c--anim #cm,
.show--consent .c--anim #cm.bar,
.show--settings .c--anim #s-inr,
.show--settings .c--anim .bar.slide #s-inr {
    opacity: 1;
    transform: scale(1);
    visibility: visible!important;
}

.show--settings .c--anim #s-cnt {
    visibility: visible!important;
}


/* Show overlays */

.force--consent.show--consent .c--anim #cm-ov,
.show--settings .c--anim #cs-ov {
    visibility: visible!important;
    opacity: 1!important;
}

#cm {
    font-family: inherit;
    padding: 1.2em 2.2em 1.825em 2.2em;
    position: fixed;
    z-index: 1;
    background: #fff;
    background: var(--cc-bg);
    max-width: 25em;
    width: 100%;
    bottom: 1.250em;
    right: 1.250em;
    box-shadow: 0 0.625em 1.875em #000000;
    box-shadow: 0 0.625em 1.875em rgba(2, 2, 3, 0.28);
    opacity: 0;
    visibility: hidden;
    transform: scale(.95);
    line-height: initial;
}

#c-ttl {
    margin: 0 0 0.6em 0;
    font-size: 1.3em;
}

#c-txt {
    margin-bottom: 1.625em;
    font-size: 0.9em;
    line-height: 1.45em;
}

.cc_div .c-bn {
    color: #40505a;
    color: var(--cc-btn-secondary-text);
    background: #e5ebef;
    background: var(--cc-btn-secondary-bg);
    padding: 1em 1.6em;
    display: inline-block;
    cursor: pointer;
    font-size: 0.85em;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    user-select: none;
    text-align: center;
}

#cm .c-bn {
    width: 48.5%;
}

#c-bns button+button,
#s-cnt button+button,
#s-c-bn {
    float: right;
}

#cm .c_link:hover,
#cm .c_link:active,
#s-cnt button+button:hover,
#s-cnt button+button:active,
#s-c-bn:active,
#s-c-bn:hover {
    background: #d8e0e6;
    background: var(--cc-btn-secondary-hover-bg);
}


/**
CookieConsent settings modal
**/

#s-cnt {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 101;
    display: table;
    height: 100%;
    visibility: hidden;
}

#s-bl {
    outline: none;
}

#s-bl .title {
    margin-top: 1.4em;
}

#s-bl .title:first-child {
    margin-top: 0;
}

#s-bl .b-tl {
    font-size: 1.1em;
}

#s-bl .b-bn {
    margin-top: 0;
}

#s-bl .b-acc .p {
    margin-top: 0;
    padding: 1em;
}

#s-cnt .b-bn .b-tl {
    display: block;
    font-family: inherit;
    font-size: 1em;
    width: 100%;
    cursor: pointer;
    position: relative;
    padding: 1.4em 6.4em 1.4em 2.7em;
    background: none;
    transition: background-color .25s ease;
}

#s-cnt .act .b-bn .b-tl {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
}

#s-cnt .b-bn .b-tl:active,
#s-cnt .b-bn .b-tl:hover {
    background: #e9eff4;
    background: var(--cc-cookie-category-block-bg-hover);
}

#s-bl .b-bn {
    position: relative;
}

#s-bl .c-bl {
    padding: 1em;
    margin-bottom: .5em;
    border: 1px solid #f1f3f5;
    border-color: var(--cc-section-border);
    transition: background-color .25s ease;
}

#s-bl .c-bl:hover {
    background: #f0f4f7;
    background: var(--cc-cookie-category-block-bg);
}

#s-bl .c-bl:last-child {
    margin-bottom: .5em;
}

#s-bl .c-bl:first-child {
    transition: none;
    padding: 0;
    margin-top: 0;
    border: none;
    margin-bottom: 2em;
}

#s-bl .c-bl:first-child:hover {
    background: transparent;
    background: unset;
}

#s-bl .c-bl.b-ex {
    margin-top: 2em;
    padding: 0;
    border: none;
    background: #f0f4f7;
    background: var(--cc-cookie-category-block-bg);
    transition: none;
}

#s-bl .c-bl.b-ex+.c-bl {
    margin-top: 2em;
}

#s-bl .c-bl.b-ex+.c-bl.b-ex {
    margin-top: 0;
}

#s-bl .c-bl.b-ex:first-child {
    margin-bottom: 1em;
}

#s-bl .c-bl.b-ex:first-child {
    margin-bottom: .5em;
}

#s-bl .b-acc {
    max-height: 0;
    overflow: hidden;
    padding-top: 0;
    margin-bottom: 0;
    display: none;
}

#s-bl .act .b-acc {
    max-height: 100%;
    display: block;
    overflow: hidden;
}

#s-cnt .p {
    font-size: 0.9em;
    line-height: 1.3em;
    margin-top: 1em;
}

.cc_div .c-tgl:disabled {
    cursor: not-allowed;
}

#c-vln {
    display: table-cell;
    vertical-align: middle;
    position: relative;
}

#cs {
    padding: 0 1.7em;
    width: 100%;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: 100%;
}

#s-inr {
    height: 100%;
    position: relative;
    max-width: 47em;
    margin: 0 auto;
    transform: scale(.96);
    opacity: 0;
    padding-top: 5.125em;
    padding-bottom: 4.9375em;
    position: relative;
    height: 100%;
    overflow: hidden;
    visibility: hidden;
    box-shadow: rgba(3, 6, 9, .26) 0px 13px 27px -5px;
}

#s-inr,
#s-hdr,
#s-bns {
    background: #fff;
    background: var(--cc-bg);
}

#s-bl {
    overflow-y: auto;
    overflow-y: overlay;
    overflow-x: hidden;
    height: 100%;
    padding: 1.5em 2.5em;
    display: block;
    width: 100%;
}

#s-bns {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 1em 2.5em;
    border-top: 1px solid #f1f3f5;
    border-color: var(--cc-section-border);
    height: 4.9375em;
}

.cc_div .cc-link {
    color: #253b48;
    color: var(--cc-btn-primary-bg);
    display: inline;
    padding-bottom: 0;
    text-decoration: none;
    cursor: pointer;
    font-weight: bold;
}

.cc_div .cc-link:hover,
.cc_div .cc-link:active {
    border-color: transparent;
}

#c-bns button:first-child,
#s-bns button:first-child {
    color: #fff;
    color: var(--cc-btn-primary-text);
    background: #253b48;
    background: var(--cc-btn-primary-bg);
}

.cc_div .c-tgl:checked~.c-tg {
    background: #253b48;
    background: var(--cc-toggle-bg-on);
}

#c-bns button:first-child:active,
#c-bns button:first-child:hover,
#s-bns button:first-child:active,
#s-bns button:first-child:hover {
    background: #1d2e38;
    background: var(--cc-btn-primary-hover-bg);
}

#s-hdr {
    position: absolute;
    top: 0;
    width: 100%;
    display: table;
    padding: 1.2em 2.5em;
    vertical-align: middle;
    z-index: 2;
    border-bottom: 1px solid #f1f3f5;
    border-color: var(--cc-section-border);
}

#s-ttl {
    display: table-cell;
    vertical-align: middle;
    font-size: 1em;
}

#s-c-bn {
    padding: 0;
    width: 1.7em;
    height: 1.7em;
    font-size: 1.6em;
    margin: 0;
    font-weight: initial;
    position: relative;
}

#s-c-bnc {
    display: table-cell;
    vertical-align: middle;
}

.cc_div span.t-lb {
    position: absolute;
    top: 0;
    z-index: -1;
    opacity: 0;
    pointer-events: none;
    overflow: hidden;
}

#c_policy__text {
    height: 31.250em;
    overflow-y: auto;
    margin-top: 1.250em;
}

#c-s-in {
    position: relative;
    transform: translateY(-50%);
    top: 50%;
    height: 100%;
    height: calc(100% - 2.5em);
    max-height: 37.5em;
}


/** works only on webkit-based browsers **/

#s-bl::-webkit-scrollbar {
    width: .9em;
    height: 100%;
    background: transparent;
    border-radius: 0 0.250em 0.250em 0;
}

#s-bl::-webkit-scrollbar-thumb {
    border: 0.25em solid var(--cc-bg);
    background: #cfd5db;
    background: var(--cc-webkit-scrollbar-bg);
    border-radius: 100em;
}

#s-bl::-webkit-scrollbar-thumb:hover {
    background: #9199a0;
    background: var(--cc-webkit-scrollbar-bg-hover);
}

#s-bl::-webkit-scrollbar-button {
    width: 10px;
    height: 5px;
}


/** custom checkbox **/


/* The container */

.cc_div .b-tg {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    display: inline-block;
    margin: auto;
    right: 1.2em;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    vertical-align: middle;
}


/* Hide the browser's default checkbox */

.cc_div .c-tgl {
    position: absolute;
    cursor: pointer;
    display: block;
}


/* Create a custom checkbox */

.cc_div .b-tg .c-tg {
    position: absolute;
    overflow: hidden;
    background: #919ea6;
    background: var(--cc-toggle-bg-off);
    transition: background-color .25s ease;
    pointer-events: none;
}

.cc_div span.t-lb,
.cc_div .b-tg,
.cc_div .b-tg .c-tg,
.cc_div .c-tgl {
    width: 3.6em;
    height: 1.6em;
    border-radius: 4em;
}

.cc_div .b-tg .c-tg.c-ro {
    cursor: not-allowed;
}

.cc_div .c-tgl~.c-tg.c-ro {
    background: #d5dee2;
    background: var(--cc-toggle-bg-readonly);
}

.cc_div .c-tgl~.c-tg.c-ro:after {
    box-shadow: none;
}


/* Style the checkmark/indicator */

.cc_div .b-tg .c-tg:after {
    content: "";
    position: absolute;
    left: 0.22em;
    top: 0.2em;
    width: 1.2em;
    height: 1.2em;
    border: none;
    box-sizing: content-box;
    background: #fff;
    background: var(--cc-toggle-knob-bg);
    box-shadow: 0 1px 2px rgb(24 32 35 / 36%);
    transition: transform .25s ease;
    border-radius: 100%;
}


/* Show the checkmark when checked */

.cc_div .c-tgl:checked~.c-tg:after {
    transform: translateX(1.95em);
}

#s-bl table,
#s-bl th,
#s-bl td {
    border: none;
}

#s-bl tbody tr {
    transition: background-color .25s ease;
}

#s-bl tbody tr:hover {
    background: #e9eff4;
    background: var(--cc-cookie-category-block-bg-hover);
}

#s-bl table {
    text-align: left;
    border-collapse: collapse;
    width: 100%;
    padding: 0;
    margin: 0;
    overflow: hidden;
}

#s-bl td,
#s-bl th {
    padding: 0.8em 0.625em;
    text-align: left;
    vertical-align: top;
    font-size: .8em;
    padding-left: 1.2em;
}

#s-bl th {
    font-family: inherit;
    padding: 1.2em 1.2em;
}

#s-bl thead tr:first-child {
    border-bottom: 1px solid #e9edf2;
    border-color: var(--cc-cookie-table-border);
}

.force--consent #s-cnt,
.force--consent #cs {
    width: 100vw;
}

#cm-ov,
#cs-ov {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    visibility: hidden;
    opacity: 0;
    background: #070707;
    background: rgba(4, 6, 8, .85);
    background: var(--cc-overlay-bg);
    display: none;
    transition: none;
}

.show--settings #cs-ov,
.c--anim #cs-ov,
.force--consent .c--anim #cm-ov,
.force--consent.show--consent #cm-ov {
    display: block;
}

#cs-ov {
    z-index: 2;
}

.force--consent .cc_div {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 25vh;
    width: 100%;
    width: 100vw;
    visibility: hidden;
    transition: visibility .25s ease;
}

.force--consent.show--consent .c--anim .cc_div,
.force--consent.show--settings .c--anim .cc_div {
    visibility: visible;
}

.force--consent #cm {
    position: absolute;
}

.force--consent #cm.bar {
    width: 100vw;
    max-width: 100vw;
}

html.force--consent.show--consent,
html.force--consent.show--settings {
    overflow-y: hidden!important;
}

html.force--consent,
html.force--consent body {
    height: auto!important;
    overflow-x: hidden!important;
}


/** END BLOCK PAGE SCROLL */


/** BEGIN ICONS **/

.cc_div .b-bn .b-tl::before,
.cc_div .act .b-bn .b-tl::before {
    border: solid #2d4156;
    border-color: var(--cc-btn-secondary-text);
    border-width: 0 2px 2px 0;
    padding: .2em;
    display: inline-block;
    position: absolute;
    content: '';
    margin-right: 15px;
    position: absolute;
    transform: translateY(-.2em) rotate(45deg);
    left: 1.2em;
    top: 1.85em;
}

.cc_div .act .b-bn .b-tl::before {
    transform: translateY(.05em) rotate(225deg);
}

.cc_div .on-i::before {
    border: solid #ffffff;
    border-color: var(--cc-toggle-knob-icon-color);
    border-width: 0 2px 2px 0;
    padding: .13em;
    display: inline-block;
    padding-bottom: .55em;
    content: '';
    margin: 0 auto;
    transform: rotate(45deg);
    margin-top: .31em;
    margin-left: .1em;
}

#s-c-bn::before,
#s-c-bn::after {
    content: '';
    position: absolute;
    left: .82em;
    top: .55em;
    height: 17px;
    width: 1.5px;
    background: #444d53;
    background: var(--cc-btn-secondary-text);
    transform: rotate(45deg);
    border-radius: 1em;
    margin: 0 auto;
}

#s-c-bn::after {
    transform: rotate(-45deg);
}

.cc_div .off-i,
.cc_div .on-i {
    height: 100%;
    width: 50%;
    position: absolute;
    right: 0;
    display: block;
    text-align: center;
    transition: opacity .25s ease;
}

.cc_div .on-i {
    left: 0;
    opacity: 0;
}

.cc_div .off-i::before,
.cc_div .off-i::after {
    right: .84em;
    top: .4em;
    content: ' ';
    height: .85em;
    width: 0.09375em;
    display: block;
    background: #cdd6dc;
    background: var(--cc-toggle-knob-icon-color);
    margin: 0 auto;
    position: absolute;
    transform-origin: center;
}

.cc_div .off-i::before {
    transform: rotate(45deg);
}

.cc_div .off-i::after {
    transform: rotate(-45deg);
}

.cc_div .c-tgl:checked~.c-tg .on-i {
    opacity: 1;
}

.cc_div .c-tgl:checked~.c-tg .off-i {
    opacity: 0;
}


/** END ICONS **/


/* Start cloud layout */

#cm.cloud {
    max-width: 50em;
    margin: 0 auto;
    text-align: center;
    left: 1.25em;
    right: 1.25em;
    overflow: hidden;
    padding: 1.3em 2.4em;
    width: unset;
}

.cc_div .cloud #c-inr {
    display: table;
    width: 100%;
}

.cc_div .cloud #c-inr-i {
    width: 70%;
    display: table-cell;
    vertical-align: top;
    padding-right: 2.4em;
}

.cc_div .cloud #c-ttl {
    font-size: 1.1em;
}

.cc_div .cloud #c-txt {
    margin-bottom: 0;
    font-size: 0.85em;
}

.cc_div .cloud #c-bns {
    min-width: 170px;
    display: table-cell;
    vertical-align: middle;
}

#cm.cloud .c-bn {
    margin: .625em 0 0 0;
    width: 100%;
    font-size: .8em;
    border: var(--border);
}

#cm.cloud .c-bn:first-child {
    margin: 0;
}

#cm.cloud.left {
    margin-right: 1.25em;
}

#cm.cloud.right {
    margin-left: 1.25em;
}


/* End cloud layout */


/* Start bar layout */

#cm.bar {
    width: 100%;
    max-width: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 0;
    position: fixed;
    padding: 2em;
}

#cm.bar #c-inr {
    max-width: 32em;
    margin: 0 auto;
}

#cm.bar #c-bns {
    max-width: 540px;
}

#cm.bar #cs {
    padding: 0;
}

.cc_div .bar #c-s-in {
    top: 0;
    transform: none;
    height: 100%;
    max-height: 100%;
}

.cc_div .bar #s-hdr,
.cc_div .bar #s-bl,
.cc_div .bar #s-bns {
    padding-left: 2em;
    padding-right: 2em;
}

.cc_div .bar #cs {
    padding: 0;
}


/* align bar to right by default */

.cc_div .bar #s-inr {
    margin: 0;
    margin-left: auto;
    margin-right: 0;
    border-radius: 0;
    max-width: 32em;
}

.cc_div .bar.left #s-inr {
    margin-left: 0;
    margin-right: auto;
}


/* Force table to not be like tables anymore */

.cc_div .bar #s-bl table,
.cc_div .bar #s-bl thead,
.cc_div .bar #s-bl tbody,
.cc_div .bar #s-bl th,
.cc_div .bar #s-bl td,
.cc_div .bar #s-bl tr,
.cc_div .bar #s-cnt {
    display: block;
}


/* Hide table headers (but not display: none;, for accessibility) */

.cc_div .bar #s-bl thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
}

.cc_div .bar #s-bl tr {
    border-top: 1px solid #e3e7ed;
    border-color: var(--cc-cookie-table-border);
}

.cc_div .bar #s-bl td {
    /* Behave  like a "row" */
    border: none;
    position: relative;
    padding-left: 35%;
}

.cc_div .bar #s-bl td:before {
    position: absolute;
    left: 1em;
    padding-right: 0.625em;
    white-space: nowrap;
    content: attr(data-column);
    color: #000;
    color: var(--cc-text);
    overflow: hidden;
    text-overflow: ellipsis;
}


/* End bar layout */


/* Positions */

#cm.top {
    bottom: auto;
    top: 1.250em;
}

#cm.left {
    right: auto;
    left: 1.250em;
}

#cm.right {
    left: auto;
    right: 1.250em;
}

#cm.bar.left,
#cm.bar.right {
    left: 0;
    right: 0;
}

#cm.bar.top {
    top: 0;
}


/* end positions */

@media screen and (max-width: 688px) {
    #cm,
    #cm.cloud,
    #cm.left,
    #cm.right {
        width: 100%;
        max-width: 100%;
        margin: 0;
        padding: 1.875em;
        right: 0;
        left: 0;
        bottom: 0;
        border-radius: 0;
        display: block;
    }
    .force--consent #cm,
    .force--consent #cm.cloud {
        width: 100vw;
        max-width: 100vw;
    }
    #cm.top {
        top: 0;
        bottom: unset;
    }
    #cm.bottom {
        bottom: 0;
        top: unset;
    }
    .cc_div .cloud #c-ttl {
        font-size: 1em;
    }
    .cc_div .cloud #c-txt {
        font-size: .7em;
    }
    #cm.cloud .c-bn {
        font-size: .85em;
    }
    .cc_div .bar #s-inr {
        max-width: 100%;
        width: 100%;
    }
    .cc_div .cloud #c-inr-i {
        padding-right: 0;
    }
    #cs {
        border-radius: 0;
        padding: 0;
    }
    #c-s-in {
        max-height: 100%;
        height: 100%;
        top: 0;
        transform: none;
    }
    .cc_div .b-tg {
        font-size: 1.25em;
        right: .9em;
    }
    #s-inr {
        margin: 0;
        padding-bottom: 8.1em;
        border-radius: unset;
    }
    #s-bl,
    .cc_div .bar #s-bl {
        padding: 1.3em;
    }
    #s-hdr,
    .cc_div .bar #s-hdr {
        padding: 1.2em 1.3em;
    }
    #s-bns,
    .cc_div .bar #s-bns {
        height: 8.1em;
        padding: 1em 1.3em;
    }
    /** dynamic table layout **/
    #s-bl table {
        width: 100%;
    }
    /* Force table to not be like tables anymore */
    #s-bl table,
    #s-bl thead,
    #s-bl tbody,
    #s-bl th,
    #s-bl td,
    #s-bl tr,
    #s-cnt {
        display: block;
    }
    /* Hide table headers (but not display: none;, for accessibility) */
    #s-bl thead tr {
        position: absolute;
        top: -9999px;
        left: -9999px;
    }
    #s-bl tr {
        border-top: 1px solid #e3e7ed;
        border-color: var(--cc-cookie-table-border);
    }
    #s-bl td {
        /* Behave  like a "row" */
        border: none;
        position: relative;
        padding-left: 35%;
    }
    #s-bl td:before {
        position: absolute;
        left: 1em;
        padding-right: 0.625em;
        white-space: nowrap;
        content: attr(data-column);
        color: #000;
        color: var(--cc-text);
        overflow: hidden;
        text-overflow: ellipsis;
    }
    #cm .c-bn,
    .cc_div .c-bn {
        width: 100%;
        margin-right: 0;
    }
    #c-bns button+button,
    #s-cnt button+button {
        margin-top: 0.625em;
        float: unset;
    }
    #cm.cloud {
        left: 0;
        right: 0;
        max-width: 100%;
        width: 100%;
    }
    #cm.cloud.right,
    #cm.cloud.left {
        margin: 0;
    }
    .cc_div .cloud #c-bns,
    .cc_div .cloud #c-inr,
    .cc_div .cloud #c-inr-i {
        display: block;
        width: auto;
        min-width: auto;
    }
    .cc_div .cloud #c-txt {
        margin-bottom: 1.625em
    }
}


/* Begin IE fixes */

.cc_div.ie #c-vln {
    height: 100%;
    padding-top: 5.62em;
}

.cc_div.ie .bar #c-vln {
    padding-top: 0;
}

.cc_div.ie #cs {
    max-height: 37.5em;
    position: relative;
    top: 0;
    margin-top: -5.625em;
}

.cc_div.ie .bar #cs {
    margin-top: 0;
    max-height: 100%;
}

.cc_div.ie #cm {
    border: 1px solid #dee6e9;
}

.cc_div.ie #c-s-in {
    top: 0;
}

.cc_div.ie .b-tg {
    padding-left: 1em;
    margin-bottom: 0.7em;
}

.cc_div.ie .c-tgl:checked~.c-tg:after {
    left: 1.95em;
}

.cc_div.ie #s-bl table {
    overflow: auto;
}

.cc_div.ie .b-tg .c-tg {
    display: none;
}

.cc_div.ie .c-tgl {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 0.2em;
    height: auto;
}

.cc_div.ie #s-cnt .b-bn .b-tl {
    padding: 1.4em 6.4em 1.4em 1.4em
}

.cc_div.ie .bar #s-bl td:before {
    display: none;
}

.cc_div.ie .bar #s-bl td {
    padding: 0.8em 0.625em 0.8em 1.2em;
}

.cc_div.ie .bar #s-bl thead tr {
    position: relative;
}

.cc_div.ie .b-tg .t-lb {
    filter: alpha(opacity=0);
}

.cc_div.ie #cm-ov,
.cc_div.ie #cs-ov {
    filter: alpha(opacity=80);
}


/** END IE FIXES **/